import * as React from 'react';
import { Link } from 'react-router-dom';

export const Header = (props) => {
    const {isAuthorizationRequired, user} = props;
    
    return <header className="header">
    <div className="container">
      <div className="header__wrapper">
        <div className="header__left">
          <Link className={`header__logo-link ${!isAuthorizationRequired ? `header__logo-link--active` : ``}`} to={`/`}>            
            <img className="header__logo" src="img/logo.svg" alt="6 cities logo" width="81" height="41" />            
          </Link>
        </div>
        <nav className="header__nav">
          <ul className="header__nav-list">
            <li className="header__nav-item user">
            <Link className="header__nav-link header__nav-link--profile" to={!isAuthorizationRequired ? `/favorites` : `/login`}>
                <div className="header__avatar-wrapper user__avatar-wrapper" style={!isAuthorizationRequired ? {backgroundImage:user.avatar_url} : {}}>
                </div>
                {!isAuthorizationRequired                 
                ? <span className="header__user-name user__name">{user.email}</span>
                : <span className="header__login">Sign in</span>
                }
              </Link>
            </li>
          </ul>
        </nav>
      </div>
    </div>
  </header>
}