import * as React from 'react';
import { connect } from 'react-redux';
import { Header } from '../header/header';
import { Operation } from '../../reducer/reducer';
import { Redirect } from 'react-router-dom';
const SignIn = (props) => {
  const { onLogin, isAuthorizationRequired, user } = props;
  return (
    <>
      {!isAuthorizationRequired
        ? <Redirect to="/"/>
        : (<React.Fragment>
          <div className="page page--gray page--login">
            <Header isAuthorizationRequired={isAuthorizationRequired} user={user} />

            <main className="page__main page__main--login">
              <div className="page__login-container container">
                <section className="login">
                  <h1 className="login__title">Sign in</h1>
                  <form className="login__form form" action="#" method="post" onSubmit={onLogin}>
                    <div className="login__input-wrapper form__input-wrapper">
                      <label className="visually-hidden">E-mail</label>
                      <input className="login__input form__input" type="email" name="email" placeholder="Email" required={true} />
                    </div>
                    <div className="login__input-wrapper form__input-wrapper">
                      <label className="visually-hidden">Password</label>
                      <input className="login__input form__input" type="password" name="password" placeholder="Password" required={true} />
                    </div>
                    <button className="login__submit form__submit button" type="submit">Sign in</button>
                  </form>
                </section>
                <section className="locations locations--login locations--current">
                  <div className="locations__item">
                    <a className="locations__item-link" href="#">
                      <span>Amsterdam</span>
                    </a>
                  </div>
                </section>
              </div>
            </main>
          </div>
        </React.Fragment>)
      }
    </>
  )
}

const mapStateToProps = (state, ownProps) => Object.assign({}, ownProps, {
  isAuthorizationRequired: state.isAuthorizationRequired,
  user: state.user,
});

const mapDispatchToProps = (dispatch) => ({
  onLogin: (evt: any): void => {
    evt.preventDefault();
    dispatch(Operation.login(evt));
  },
});

export {
  SignIn,
}

export default connect(mapStateToProps, mapDispatchToProps)(SignIn);